.precipitate-info {
    height: 20%;
    width: 25%;
    position: absolute;
    top: 10%;
    right: 1%;
    /* left: 50%; */
    transform: translateX(-50%);
    background-color: rgba(0, 0, 0, 0.7);
    padding: 10px 20px;
    border-radius: 5px;
    font-size: 16px;

    animation: fadeIn 0.5s ease-in;
}

@keyframes fadeIn {
    from { opacity: 0; }
    to { opacity: 1; }
}