/* General container styles */
.chemistry-lab-container {
    max-width: 23rem;
    margin: 0 auto;
    padding: 2rem;
    background-color: #f9fafb4d;
    /* border-radius: 0.75rem; */
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  }
  
  /* Process selector styles */
  .process-selector {
    display: flex;
    justify-content: center;
    gap: 1rem;
    margin-bottom: 1.5rem;
  }
  
  .process-btn {
    padding: 0.5rem 1.5rem;
    font-weight: 500;
    transition: background-color 0.3s ease;
    background-color: #e5e7eb;
    color: #1f2937;
    margin: 5px !important;
    padding: 8px 16px !important;
    border: none;
    cursor: pointer;
  }
  
  .process-btn.active {
    background-color: #3b82f6;
    color: #fff;
  }
  
  .process-btn:hover {
    background-color: #d1d5db;
    color: #6b7280;
  }
  
  /* Process details section */
  .process-details {
    background-color: #ffffffea;
    padding: 1rem !important;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  }
  
  .process-header {
    display: flex;
    align-items: center;
    margin-bottom: 1rem;
    
  }
  
  .process-number {
    width: 2rem;
    height: 2rem;
    background-color: #13014f;
    color: #fff;
    display: flex;
    align-items: center;
    justify-content: center;
    font-weight: bold;
    border-radius: 50%;
    margin-right: 1rem !important;
    margin-left: 1rem !important;

  }
  
  .process-title {
    font-size: 1rem;
    font-weight: bold;
    color: #13014f;
  }
  
  .process-description {
    font-size: 10px;
    color: #6b7280;
    font-weight: 600;
  }
  
  /* Steps section */
  .process-steps {
    margin-left: 1rem;
    padding-left: 1.5rem;
    border-left: 2px solid #bfdbfe;
  }
  
  .step {
    margin-bottom: 1rem;
    position: relative;
    padding:0.5rem !important;
  }
  
  .step.active-step {
    font-weight: 500;
  }
  
