  .step-text {
    font-size: 0.875rem;
    color: #1f2937;
  }
  
  .step-text.highlighted {
    color: #13014f;
  }
  
  /* Conclusion section */
  .process-conclusion {
    margin-top: 1rem;
    padding-top: 0.75rem;
    border-top: 2px solid #e5e7eb;
    
  }
  
  .process-conclusion p {
    color: #238312;
    font-weight: 500;
    font-size: 0.875rem;
    padding:0.5rem !important;
  }
  