* {
    padding: 0px !important;
    margin: 0px !important;
    overflow: hidden;
    font-family: 'Roboto', sans-serif;

}

.canvasContainer {
    position: relative;
    width: 100vw !important;
    height: 100vh !important;
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    overflow: hidden;
}

.canvasContainer__maincanvas {
    position: relative;
    width: 100%;
    height: 100%;
}

.main {
    position: relative;
    width: 100%;
    height: 100%;
    overflow: hidden;
    overflow-x: hidden;
}

.Controls {
    position: absolute;
    display: flex;
    flex-direction: column;
    width: max-content;
    height: max-content;
    background-color: rgb(255, 255, 255, 0.3);
    z-index: 1;
    margin: 0px auto;
    padding: 10px !important;
    top: 10%;
    left: 0;
}



.Sublimation {
    display: flex;
    flex-direction: column;

    margin: 5px 0 !important;
}

.btmSumblimation,
.btmNext {
    border: none;
    position: relative;
    margin: 5px !important;
    padding: 8px 16px !important;
    color: grey;
    cursor: pointer;


}

.ExpTitle {
    position: absolute;
    display: flex;
    flex-direction: column;
    width: 100%;
    height: max-content;
    display: flex;
    justify-content: center;
    align-items: center;
    /* background-color: rgb(255, 255, 255,0.3 ); */
    color: aliceblue;
    font-size: 0.35in;
    z-index: 1;
    margin: 0px auto;
    padding: 10px !important;
    top: 0%;
    left: 0%;
}

.btmNext.glow {
    animation: glow 2s ease-in-out infinite alternate;
    background-color: #4CAF50;
    color: white;
}

@keyframes glow {
    from {
        box-shadow: 0 0 5px #4CAF50, 0 0 10px #4CAF50, 0 0 15px #4CAF50;
    }

    to {
        box-shadow: 0 0 10px #4CAF50, 0 0 20px #4CAF50, 0 0 30px #4CAF50;
    }
}

.btmNext:disabled {
    background-color: #cccccc;
    cursor: not-allowed;
    animation: none;
    box-shadow: none;
}

.btmSumblimation:disabled {
    background-color: #cccccc;
    cursor: not-allowed;
}

.glow-effect {
    border: 1px solid #3ca8e7;
    box-shadow: 0 0 20px #3ca8e7, 0 0 2px #3ca8e7;
    transition: all 0.3s ease;
}

/*Stepper CSS*/

.stepper {
    position: absolute;
    display: flex;
    flex-direction: column;
    width: max-content;
    height: max-content;
    background-color: rgb(255, 255, 255, 0.3);
    z-index: 1;
    margin: 0px auto;
    padding: 10px !important;
    top: 10%;
    right: 0;
}

/* Stepper.css */
.stepper-list {
    list-style-type: none;
    padding: 0;
    margin: 0;
    display: flex;
    flex-direction: column;
    gap: 1rem;
}

.step-item {
    display: flex;
    align-items: center;
    padding: 1rem;
    border-radius: 0.5rem;
    transition: all 0.3s ease;
}

.step-content {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
}

.step-sr-only {
    position: absolute;
    width: 1px;
    height: 1px;
    padding: 0;
    margin: -1px;
    overflow: hidden;
    clip: rect(0, 0, 0, 0);
    white-space: nowrap;
    border-width: 0;
}

.step-title {
    margin: 0;
    font-size: 1rem;
    font-weight: 500;
}

.step-icon,
.step-icon-arrow {
    width: 1.5rem;
    height: 1.5rem;
}

.step-completed {
    background-color: #d1fae5;
    color: #065f46;
}

.step-active {
    background-color: #eff6ff;
    color: #1e40af;
    border-left: 4px solid #1e40af;
}

.step-inactive {
    background-color: #f3f4f6;
    color: #6b7280;
}







.confetti-container {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    pointer-events: none;
    z-index: 1000;
    overflow: hidden;
}

.confetti {
    position: absolute;
    width: 10px;
    height: 10px;
    background: #ff0000;
    opacity: 0.7;
    animation: fall 3s linear forwards;
}

@keyframes fall {
    0% {
        transform: translateY(-100vh) rotate(0deg);
        opacity: 1;
    }

    100% {
        transform: translateY(100vh) rotate(720deg);
        opacity: 0;
    }
}
