/* MainHomePage.css */
.homepage-container {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* Soft blue-gray background with a subtle animated gradient */
  background: linear-gradient(45deg, #e5ecf4, #d6e1f0, #e5ecf4, #d6e1f0);
  background-size: 400%;
  font-family: 'Roboto', sans-serif;
  color: #2c3e50;
  animation: gradientShift 15s ease infinite; /* Animated background */
}

/* Background gradient animation */
@keyframes gradientShift {
  0% { background-position: 0% 50%; }
  50% { background-position: 100% 50%; }
  100% { background-position: 0% 50%; }
}

.homepage-header {
  text-align: center;
  padding: 50px 20px;
  /* Neumorphic background with a subtle gradient */
  background: linear-gradient(145deg, #e5ecf4, #f0f5fa);
  box-shadow: inset 5px 5px 10px #d1d9e6, inset -5px -5px 10px #f9faff;
  border-bottom: none;
  position: relative;
  overflow: hidden;
}

/* Add a subtle glow effect to the header */
.homepage-header::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: radial-gradient(circle, rgba(70, 130, 180, 0.1) 0%, transparent 70%);
  opacity: 0.5;
}

.homepage-header h1 {
  font-size: 2.8rem;
  font-weight: 700;
  margin: 0;
  color: #2c3e50;
  
  text-shadow: 2px 2px 4px #d1d9e6, -2px -2px 4px #f9faff;
  /* Add a subtle gradient to the text */
  background: linear-gradient(45deg, #2c3e50, #4682b4);
  background-clip: text;
  background-clip: text;
 
  -webkit-text-fill-color: transparent;
}

.homepage-header p {
  font-size: 1.2rem;
  margin-top: 15px;
  color: #5c6b7a;

  font-style: italic; /* Add a touch of elegance */
}

.experiment-selection {
    height: 100px;
    flex: 1 1;
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    height: 100px;
    width: 100%;
    
    align-items: center;
    gap: 40px;
    padding: 50px 20px;
}

.experiment-card {
  /* Neumorphic background with a subtle gradient overlay */
  background: linear-gradient(145deg, #e5ecf4, #f0f5fa);
  box-shadow: 8px 8px 16px #d1d9e6, -8px -8px 16px #f9faff;
  border-radius: 25px;
  padding: 40px;
  width: 320px;
  height: 200px; /* Increased height for better spacing */
  display: flex;
  flex-direction: column;
  text-align: center;
  transition: all 0.3s ease;
  cursor: pointer;
  border: none;
  align-items: center;
  justify-content: center;
  position: relative;
  overflow: hidden;
}

/* Add a subtle glow effect to the card on hover */
.experiment-card:hover {
  box-shadow: inset 5px 5px 10px #d1d9e6, inset -5px -5px 10px #f9faff, 0 0 15px rgba(70, 130, 180, 0.3);
  transform: translateY(2px);
}

/* Add a radial gradient overlay for a futuristic touch */
.experiment-card::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: radial-gradient(circle, rgba(70, 130, 180, 0.1) 0%, transparent 70%);
  opacity: 0;
  transition: opacity 0.3s ease;
}

.experiment-card:hover::before {
  opacity: 0.5;
}

.experiment-icon {
  font-size: 2.5rem;
  color: #4682b4;
  margin-bottom: 20px;
  filter: drop-shadow(2px 2px 3px #d1d9e6) drop-shadow(-2px -2px 3px #f9faff);
  transition: transform 0.3s ease; /* Add animation for icons */
}

/* Icon animation on hover */
.experiment-card:hover .experiment-icon {
  transform: scale(1.2) rotate(5deg); /* Scale and rotate for a playful effect */
  color: #2c3e50; /* Darker color on hover */
}

.experiment-card h2 {
  font-size: 1.8rem;
  font-weight: 600;
  margin: 0 0 15px; /* Increased margin-bottom for spacing */
  margin-top: 10px; /* Added margin-top as requested */
  color: #2c3e50;
  /* Add a subtle gradient to the text */
  background: linear-gradient(45deg, #2c3e50, #4682b4);
  background-clip: text;
  -webkit-text-fill-color: transparent;
}

.experiment-card p {
  font-size: 1rem;
  margin: 0 0 20px;
  color: #5c6b7a;
}

.explore-button {
  /* Neumorphic button with a gradient */
  background: linear-gradient(45deg, #e5ecf4, #f0f5fa);
  box-shadow: 5px 5px 10px #d1d9e6, -5px -5px 10px #f9faff;
  border: none;
  padding: 12px 25px;
  display: flex;
  border-radius: 15px;
  font-size: 1rem;
  height: 25%;
  width: 55%;
  font-weight: 500;
  color: #4682b4;
  cursor: pointer;
  margin-top: 15px; /* Added margin-top as requested */
  transition: all 0.3s ease;
  justify-content: center;
  align-items: center;
  position: relative;
  overflow: hidden;
}

/* Add a hover effect with a gradient overlay */
.explore-button:hover {
  box-shadow: inset 3px 3px 6px #d1d9e6, inset -3px -3px 6px #f9faff;
  transform: translateY(1px);
  color: #2c3e50;
  background: linear-gradient(45deg, #f0f5fa, #e5ecf4);
}

/* Add a subtle glow effect to the button on hover */
.experiment-card:hover .explore-button {
  box-shadow: inset 3px 3px 6px #d1d9e6, inset -3px -3px 6px #f9faff, 0 0 10px rgba(70, 130, 180, 0.3);
}

.homepage-footer {
  text-align: center;
  padding: 20px;
  background: linear-gradient(145deg, #e5ecf4, #f0f5fa);
  box-shadow: inset 5px 5px 10px #d1d9e6, inset -5px -5px 10px #f9faff;
  border-top: none;
  font-size: 0.9rem;
  color: #5c6b7a;
  position: relative;
  overflow: hidden;
}

/* Add a subtle glow effect to the footer */
.homepage-footer::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: radial-gradient(circle, rgba(70, 130, 180, 0.1) 0%, transparent 70%);
  opacity: 0.5;
}

/* Fade-in animation for a smooth entrance */
@keyframes fadeIn {
  from { opacity: 0; transform: translateY(20px); }
  to { opacity: 1; transform: translateY(0); }
}

.homepage-header,
.experiment-selection,
.homepage-footer {
  animation: fadeIn 1s ease-out forwards;
}

/* Responsive adjustments */
@media (max-width: 768px) {
  .experiment-selection {
    flex-direction: column;
    gap: 30px;
  }

  .experiment-card {
    width: 90%;
    max-width: 320px;
    height: 220px; /* Adjusted height for mobile */
  }

  .homepage-header h1 {
    font-size: 2.2rem;
  }

  .homepage-header p {
    font-size: 1rem;
  }

  .explore-button {
    width: 70%; /* Slightly wider button on mobile */
  }

  
}