* {
    padding: 0px !important;
    margin: 0px !important;
    overflow: hidden;
}

.btn_state1{
    width: 10%;
    height:4%;
    top: 10px;
    left: 10px;
    z-index: 1;
    margin: 100px;
    position: absolute;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 0.2in;
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    color: #ffffff;
    background-color: rgba(100, 99, 99, 0.452);
    border-radius: 3px;
    cursor: pointer;
}
