* {
    padding: 0px !important;
    margin: 0px !important;
    overflow: hidden;
    font-family: 'Roboto', sans-serif;

}

.plane-label {
    height: 25px;
    width: 150px;
    display: flex;
    align-items: center;

    justify-content: center;
    color: rgb(255, 255, 255);
    background-color: rgba(100, 99, 99, 0.452);
    padding: 5% 15px;
    border-radius: 3px;

    font-size: 0.2in;
    white-space: nowrap;
}

.Information {
    height: 55%;
    width: 25%;
    position: absolute;
    display: flex;
    flex-direction: column;
    z-index: 1;
    top: 20%;
    left: 5%;
    border-radius: 2%;
    transition: opacity 0.5s ease, background-color 0.5s ease;
    /* Smooth transition */
}

.fade-in {
    opacity: 1;
}

.fade-out {
    opacity: 0;
}

.PlateTitle {
    width: 100%;
    height: 10%;
    /* background-color:white ; */
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 0.3in;
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    color: #ffffff;
}

.img img {
    width: 100%;
    height: auto;
    transition: opacity 0.5s ease;
    /* Smooth transition for image */
}